import React from 'react';
import './start.css';

export class Start extends React.Component {
    constructor (props) {
        super(props);
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick () {
        this.props.changePage('form');
    }
    render () {
        if (this.props.page === 'start') {
            return (
                <div className='start'>
                    <h1>Mineral Detective</h1>
                    <button onClick={this.handleClick}>Start</button>
                    <p>Supported by</p>
                    <div id='sponsors'>
                        <img 
                            src={`${process.env.PUBLIC_URL}/RSM_logo.png`}
                            id='rsm'/>
                        <img 
                            src={`${process.env.PUBLIC_URL}/Imperial_logo.png`}
                            id='imperial'/>
                    </div>
                </div>
            );
        } else {
            return (<div></div>);
        }
    }
}